@use "base/variables" as v;
@use "base/mixins" as m;

.form {
    .contenedor__bloque-info {
        margin-top: 2rem;
        display: flex;
        flex-direction: column;
        .label,
        .input {
            font-family: v.$fuente;
            width: 100%;
        }
        .label {
            margin-bottom: 1rem;
        }
        .input {
            padding: 1rem;
            background-color: v.$color-cuaternario;
            border: 0.2rem solid v.$color-secundario;
            outline: none;
            border-radius: 0.5rem;
            caret-color: v.$color-terciario;
            color: v.$color-terciario;
            &::placeholder {
                color: darken(v.$color-blanco, 25%);
                font-weight: 300;
            }
        }
        .input--textarea {
            resize: none;
            height: 10rem;
        }
        .boton {
            cursor: pointer;
            padding: 2rem;
            font-family: v.$fuente;
            border: none;
            background-color: v.$color-primario;
            color: v.$color-cuaternario;
            border-radius: 0.5rem;
            transition: background-color ease 0.3s, color ease 0.3s;
            &:hover {
                background-color: v.$color-secundario;
                color: v.$color-blanco;
            }
        }
    }
}
