@use "variables" as v;
@use "mixins" as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
    scroll-behavior: smooth;
}
body {
    min-height: 100%;
    font-size: 1.6rem;
    background-color: v.$color-cuaternario;
    font-family: v.$fuente;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}
// .contenedor {
//     width: 90%;
//     max-width: 180rem;
//     margin: 0 auto;
// }
.contenedor {
    width: 100%;
}
.contenedor-flex {
    width: 100%;
    display: flex;
    justify-content: center;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
.contenedor__boton-subir {
    position: fixed;
    z-index: 19;
    background-color: v.$color-secundario;
    border-radius: 50%;
    width: 5rem;
    height: 5rem;

    bottom: 1rem;
    right: 1rem;
    box-shadow: 2px 2px 10px 3px v.$color-primario;
    a {
        width: 100%;
        height: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        border-radius: 50%;
        .icono {
            width: 3rem;
            height: 3rem;
            color: v.$color-cuaternario;
        }

        &:hover {
            background-color: v.$color-terciario;
            transition: background-color ease 0.3s;
            .icono {
                transition: color ease 3s, width ease 0.3s, height ease 0.3s;
                color: v.$color-blanco;
                width: 3.4rem;
                height: 3.4rem;
            }
        }
    }
}
