html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  font-size: 1.6rem;
  background-color: #cc9544;
  font-family: "Rubik", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.contenedor {
  width: 100%;
}

.contenedor-flex {
  width: 100%;
  display: flex;
  justify-content: center;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.contenedor__boton-subir {
  position: fixed;
  z-index: 19;
  background-color: #361500;
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
  bottom: 1rem;
  right: 1rem;
  box-shadow: 2px 2px 10px 3px #1c0a00;
}
.contenedor__boton-subir a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 50%;
}
.contenedor__boton-subir a .icono {
  width: 3rem;
  height: 3rem;
  color: #cc9544;
}
.contenedor__boton-subir a:hover {
  background-color: #603601;
  transition: background-color ease 0.3s;
}
.contenedor__boton-subir a:hover .icono {
  transition: color ease 3s, width ease 0.3s, height ease 0.3s;
  color: #fff;
  width: 3.4rem;
  height: 3.4rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Rubik", sans-serif;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 3.2rem;
}

h2,
h3 {
  font-size: 3rem;
}

.header {
  width: 100%;
  background-color: #361500;
  text-align: center;
  position: relative;
  box-shadow: 4px 4px 16px 5px #1c0a00;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  z-index: 40;
  padding: 2rem 1rem;
}
.header .contenedor-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row-reverse;
  cursor: pointer;
}
.header .contenedor-logo .titulo-pagina {
  padding: 3rem;
  font-weight: 400;
  color: #fff;
}
.header .contenedor-logo .contenedor__imagen-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 10rem;
  padding: 1rem;
}

/*
            nav.style.position = 'fixed';
            nav.style.zIndex = 30;
            nav.style.top = 0; //box-shadow: inset 4px 4px 16px 5px v.$color-primario;
            nav.style.boxShadow = 'inset -4px -4px 16px 5px red'; 

            nav.style.position = 'relative';
            nav.style.zIndex = 8;

            */
.menu-sticky {
  position: fixed;
  z-index: 30;
  padding-top: 1rem !important;
  top: 2rem;
}

.nav {
  background-color: #603601;
  padding-top: 3rem;
  padding-right: 2rem;
  padding-left: 2rem;
  padding-bottom: 1rem;
  margin-top: -2rem;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  box-shadow: 4px 4px 16px 5px #1c0a00;
}
.nav .ocultar {
  display: none;
}
.nav .menu {
  width: 100%;
  text-align: center;
  padding-left: 0;
}
.nav .menu .contenedor-enlace {
  list-style: none;
  padding: 1rem 2rem;
  border-top: 1px solid #cc9544;
}
.nav .menu .contenedor-enlace:last-of-type {
  border-bottom: 1px solid #cc9544;
}
.nav .menu .contenedor-enlace .enlace {
  color: #fff;
  font-size: 2.2rem;
}
.nav .menu .contenedor-enlace:hover {
  background-color: #cc9544;
  border-radius: 2rem;
  transition: background-color ease 1s, border-radius ease 1s;
  cursor: pointer;
}
.nav .menu .contenedor-enlace:hover .enlace {
  color: #1c0a00;
  transition: color ease 0.3s;
}

.contenedor-icono {
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  background-color: #1c0a00;
  box-shadow: 4px 4px 16px 5px #1c0a00;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color ease 0.3s, color ease 0.3s;
}
.contenedor-icono .icono {
  width: 4rem;
  height: 4rem;
  color: #cc9544;
}
.contenedor-icono:hover {
  background-color: #361500;
  border-radius: 50%;
}
.contenedor-icono:hover .icono {
  color: #fff;
}

@media (min-width: 768px) {
  .nav .contenedor-icono {
    display: none !important;
  }
  .nav .menu {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
  }
  .nav .menu .contenedor-enlace {
    padding: none;
    border-top: none;
  }
  .nav .menu .contenedor-enlace:last-of-type {
    border-bottom: none;
  }
}
.hero-image {
  background-image: url("/public/build/img/fondos_almajluf/fondo_01.jpg");
  background-position: center right;
  background-size: cover;
  width: 100%;
  height: 60rem;
  margin-top: -2rem;
  z-index: -10;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
}
.hero-image__texto {
  z-index: 15;
  font-size: 5rem;
  color: #fff;
  position: relative;
  padding: 4rem 2rem 2rem 2rem;
  font-weight: 400;
  text-transform: capitalize;
  text-align: left;
}
.hero-image .contenedor-logo {
  width: 18rem;
  height: 27rem;
  position: relative;
  z-index: 15;
  margin: 0 auto;
}
.hero-image .contenedor-logo .logo {
  background-image: url("../img/logo_almajluf_original_fondo-blanco.png");
  background-size: cover;
  width: 100%;
  height: 100%;
  padding: 1rem;
  position: relative;
}
.hero-image::before {
  position: absolute;
  content: "";
  background-color: rgba(0, 0, 0, 0.78);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  box-shadow: 4px 4px 16px 5px #1c0a00;
}

.section {
  box-shadow: 4px 4px 16px 5px #1c0a00;
  background-color: #603601;
  border-bottom: 1rem solid #361500;
  border-radius: 1rem;
  margin-bottom: 5rem;
  position: relative;
}
.section::before {
  content: "";
  width: 7rem;
  height: 7rem;
  border-radius: 50%;
  background-color: #361500;
  position: absolute;
  top: 7.2rem;
  z-index: 10;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: -44.5px 22px 0 -20px #603601, 44.5px 22px 0 -20px #603601, -30.5px 4px 0 -20px #361500, 30.5px 4px 0 -20px #361500;
}
.section .contenedor-icono {
  background: none;
  background-color: #cc9544;
  cursor: auto;
  position: absolute;
  top: 8.2rem;
  right: 50%;
  transform: translateX(50%);
  box-shadow: none;
  z-index: 15;
}
.section .contenedor-icono .icono {
  color: #1c0a00;
}
.section .contenedor-icono--taza .icono {
  position: absolute;
  bottom: 4px;
  left: 6px;
}
.section:last-of-type {
  margin-bottom: 0;
}
.section .subtitulo-pagina,
.section .informacion-section {
  color: #fff;
  position: relative;
  z-index: 10;
}
.section .subtitulo-pagina {
  background-color: #361500;
  border-radius: 1rem 1rem 0 0;
  font-weight: 400;
  text-align: center;
  padding: 4rem 2rem;
}
.section .informacion-section {
  font-size: 2rem;
  padding: 4rem 2rem 2rem 2rem;
  text-align: center;
  font-weight: 300;
  line-height: 1.6;
}

.section__contactanos .informacion-section {
  text-align: start;
}

.producto {
  height: 46rem;
  border-radius: 2rem;
  transition: height ease 0.5s;
  position: relative;
  margin-top: 10rem !important;
  background-color: #361500;
  text-align: center;
}
.producto:hover {
  background-color: #603601;
}
.producto__imagen {
  width: 22rem;
  height: 22rem;
  position: absolute;
  border-radius: 2rem;
  overflow: hidden;
  left: 50%;
  transform: translateX(-50%);
  top: -8rem;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.35);
  transition: width ease 0.5s, height ease 0.5s;
}
.producto__imagen img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.producto__nombre {
  margin-top: 16rem;
  padding: 1rem;
  color: #fff;
  font-weight: 400;
  font-size: 2.4rem;
  transition: margin-top ease 0.5s;
}
.producto__precio {
  color: #cc9544;
  font-size: 2rem;
  font-weight: 800;
  padding: 1rem;
}
.producto__porcion {
  color: #d1a058;
}
.producto__size, .producto__version {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 1rem;
  opacity: 1;
  transition: opacity ease 0.5s;
}
.producto__size .contenedorLabelInputSize,
.producto__size .contenedorLabelInputVersion, .producto__version .contenedorLabelInputSize,
.producto__version .contenedorLabelInputVersion {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-weight: 100;
}
.producto__size .contenedorLabelInputSize input,
.producto__size .contenedorLabelInputVersion input, .producto__version .contenedorLabelInputSize input,
.producto__version .contenedorLabelInputVersion input {
  margin-top: 1rem;
  cursor: pointer;
}
.producto__size .contenedorLabelInputSize label,
.producto__size .contenedorLabelInputVersion label, .producto__version .contenedorLabelInputSize label,
.producto__version .contenedorLabelInputVersion label {
  cursor: pointer;
}
.producto__size .contenedorLabelInputVersion, .producto__version .contenedorLabelInputVersion {
  font-size: 1.4rem;
}
.producto__descripcion {
  text-align: center !important;
  opacity: 1;
  padding: 1rem 2rem;
  transition: opacity ease 0.5s;
  color: #fff;
  font-weight: 300;
}
.producto__descripcion span {
  color: #cc9544;
  font-weight: 800 !important;
}

.contenedor-productos {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.contenedor-productos .section__productos {
  margin: 5rem 2rem;
  width: 28rem;
}
.contenedor-productos .productos {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.contenedor-productos .producto {
  box-shadow: 4px 4px 16px 5px #1c0a00;
  width: 28rem;
  margin: 2rem;
}
.contenedor-productos .producto span {
  color: #cc9544;
  font-weight: 400;
}

.contenedor {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenedor .section__conocenos {
  margin: 5rem 2rem;
  width: 28rem;
}

.contenedor {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenedor .section__despachos {
  margin: 5rem 2rem;
  width: 28rem;
}

.contenedor {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenedor .section__contactanos {
  margin: 5rem 2rem;
  width: 28rem;
}
.contenedor .section__contactanos .contenedor__bloque-info {
  position: relative;
}
.contenedor .section__contactanos .contenedor__bloque-info input {
  position: relative;
  right: 0.4rem;
}
.contenedor .section__contactanos .contenedor__bloque-info input,
.contenedor .section__contactanos .contenedor__bloque-info textarea {
  padding-right: 3.2rem;
}
.contenedor .section__contactanos .contenedor__bloque-info .boton {
  padding: 2rem;
  right: 0;
}
.contenedor .section__contactanos .contenedor__bloque-info .indicador {
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #b3b3b3;
  top: 6.1rem;
  right: 2rem;
}
.contenedor .section__contactanos .valido .indicador {
  background-color: #0f0;
  box-shadow: 0 0 0.5rem #0f0, 0 0 1rem #0f0, 0 0 2rem #0f0, 0 0 4rem #0f0;
}
.contenedor .section__contactanos .no--valido .indicador {
  background-color: #f00;
  box-shadow: 0 0 0.5rem #f00, 0 0 1rem #f00, 0 0 2rem #f00, 0 0 4rem #f00;
}

.contenedor--ventana-emergente {
  display: none;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  border: 1px solid rgba(0, 0, 0, 0.18);
}
.contenedor--ventana-emergente .ventana-emergente {
  position: fixed;
  width: 28rem;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.58);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 30;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 1rem;
}
.contenedor--ventana-emergente .ventana-emergente::before {
  content: "";
  position: relative;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: black;
  z-index: 50;
}
.contenedor--ventana-emergente .ventana-emergente__titulo {
  padding: 2rem;
  font-family: "Rubik", sans-serif;
  font-weight: 300;
  color: red;
}
.contenedor--ventana-emergente .ventana-emergente__mensaje {
  padding: 2rem;
  font-weight: 400;
}
.contenedor--ventana-emergente .ventana-emergente__boton {
  padding: 2rem;
}
.contenedor--ventana-emergente .ventana-emergente__boton button {
  padding: 1rem 4rem;
  border-radius: 0.5rem;
  border: none;
  background-color: #361500;
  color: #cc9544;
  box-shadow: 0 0.4rem 0.8rem 0 #120600;
  cursor: pointer;
}

@media (min-width: 480px) {
  .contenedor .section__contactanos {
    width: 44rem;
  }
}
