@use "base/variables" as v;
@use "base/mixins" as m;

.section {
    box-shadow: 4px 4px 16px 5px v.$color-primario;
    background-color: v.$color-terciario;
    border-bottom: 1rem solid v.$color-secundario;
    border-radius: 1rem;
    margin-bottom: 5rem;
    position: relative;
    &::before {
        content: "";
        width: 7rem;
        height: 7rem;
        border-radius: 50%;
        background-color: v.$color-secundario;
        position: absolute;
        top: 7.2rem;
        z-index: 10;
        left: 50%;
        transform: translateX(-50%);
        box-shadow: -44.5px 22px 0 -20px v.$color-terciario, 44.5px 22px 0 -20px v.$color-terciario,
            -30.5px 4px 0 -20px v.$color-secundario, 30.5px 4px 0 -20px v.$color-secundario;
    }
    .contenedor-icono {
        background: none;
        background-color: v.$color-cuaternario;
        cursor: auto;
        position: absolute;
        top: 8.2rem;
        right: 50%;
        transform: translateX(50%);
        //box-shadow: 4px 4px 16px 5px v.$color-primario;
        box-shadow: none;
        z-index: 15;
        .icono {
            color: v.$color-primario;
        }
    }
    .contenedor-icono--taza {
        .icono {
            position: absolute;
            bottom: 4px;
            left: 6px;
        }
    }

    &:last-of-type {
        margin-bottom: 0;
    }
    .subtitulo-pagina,
    .informacion-section {
        color: v.$color-blanco;
        position: relative;
        z-index: 10;
    }
    .subtitulo-pagina {
        background-color: v.$color-secundario;
        //clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 2rem), 0% 100%);
        border-radius: 1rem 1rem 0 0;
        font-weight: 400;
        text-align: center;
        padding: 4rem 2rem;
    }

    .informacion-section {
        font-size: 2rem;
        padding: 4rem 2rem 2rem 2rem;
        text-align: center;
        font-weight: 300;
        line-height: 1.6;
    }
}

.section__contactanos {
    .informacion-section {
        text-align: start;
    }
}

.producto {
    //height: 24rem;
    height: 46rem;
    border-radius: 2rem;
    transition: height ease 0.5s;
    position: relative;
    margin-top: 10rem !important;
    background-color: v.$color-secundario;
    text-align: center;

    &:hover {
        //height: 40rem;
        background-color: v.$color-terciario;
        // .producto__imagen {
        //     // width: 22rem;
        //     // height: 22rem;
        // }
        // .producto__nombre {
        //     // margin-top: 16rem;
        // }
        // .producto__size,
        // .producto__descripcion {
        //     // opacity: 1;
        // }
    }

    &__imagen {
        // width: 18rem;
        // height: 18rem;
        width: 22rem;
        height: 22rem;

        position: absolute;
        border-radius: 2rem;
        overflow: hidden;
        left: 50%;
        transform: translateX(-50%);
        top: -8rem;
        box-shadow: 0 15px 50px rgba(0, 0, 0, 0.35);
        transition: width ease 0.5s, height ease 0.5s;
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__nombre {
        // margin-top: 12rem;
        margin-top: 16rem;
        padding: 1rem;
        color: v.$color-blanco;
        font-weight: 400;
        font-size: 2.4rem;
        transition: margin-top ease 0.5s;
    }
    &__precio {
        color: v.$color-cuaternario;
        font-size: 2rem;
        font-weight: 800;
        padding: 1rem;
    }
    &__porcion {
        color: lighten(v.$color-cuaternario, 5%);
    }
    &__size,
    &__version {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding: 1rem;
        //opacity: 0;
        opacity: 1;
        transition: opacity ease 0.5s;
        .contenedorLabelInputSize,
        .contenedorLabelInputVersion {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            color: v.$color-blanco;
            font-weight: 100;

            input {
                margin-top: 1rem;
                cursor: pointer;
            }
            label {
                cursor: pointer;
            }
        }
        .contenedorLabelInputVersion {
            font-size: 1.4rem;
        }
    }

    &__descripcion {
        text-align: center !important;
        //opacity: 0;
        opacity: 1;
        padding: 1rem 2rem;
        transition: opacity ease 0.5s;
        color: v.$color-blanco;
        font-weight: 300;
        span {
            color: v.$color-cuaternario;
            font-weight: 800 !important;
        }
    }
}

