.contenedor-productos {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .section__productos {
        margin: 5rem 2rem;
        width: 28rem;
    }
    .productos {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    .producto {
        box-shadow: 4px 4px 16px 5px v.$color-primario;
        width: 28rem;
        margin: 2rem;
        span {
            color: v.$color-cuaternario;
            font-weight: 400;
        }
    }
}
