@use "base/variables" as v;
@use "base/mixins" as m;

.hero-image {
    background-image: url("/public/build/img/fondos_almajluf/fondo_01.jpg");
    background-position: center right;
    background-size: cover;
    width: 100%;
    height: 60rem;
    margin-top: -2rem;
    z-index: -10;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    &__texto {
        z-index: 15;
        font-size: 5rem;
        color: v.$color-blanco;
        position: relative;
        padding: 4rem 2rem 2rem 2rem;
        font-weight: 400;
        text-transform: capitalize;
        text-align: left;
    }
    .contenedor-logo {
        width: 18rem;
        height: 27rem;
        position: relative;
        z-index: 15;
        margin: 0 auto;
        .logo {
            //background-image: url("/public/build/img/logo_almajluf_original_fondo-blanco.png");
            background-image: url("../img/logo_almajluf_original_fondo-blanco.png");
            //background-position: center center;
            background-size: cover;
            width: 100%;
            height: 100%;
            padding: 1rem;
            //height: 23rem;
            //background-color: red;
            position: relative;
        }
    }

    &::before {
        position: absolute;
        content: "";
        background-color: rgba(v.$color-negro, 0.78);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        border-bottom-left-radius: 2rem;
        border-bottom-right-radius: 2rem;
        box-shadow: 4px 4px 16px 5px v.$color-primario;
    }
}
