@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-footer {
    .footer {
        background-color: v.$color-primario;
        padding: 4rem 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        flex-direction: column;
        border-top-left-radius: 2rem;
        border-top-right-radius: 2rem;
        box-shadow: -4px -4px 16px 5px v.$color-primario;
        .contenedor-rrss {
            width: 100%;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            padding-bottom: 3rem;
            .contenedor-icono {
                cursor: pointer;
                width: 5rem;
                height: 5rem;
                background-color: v.$color-secundario;
                padding: 0.8rem;
                position: relative;
                transition: background-color ease 0.5s, transform ease 0.5s;
                a {
                    margin: 0;
                    padding: 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-align: center;
                    width: 3.4rem;
                    height: 3.4rem;
                }
                .icono {
                    color: v.$color-cuaternario;
                }
                &:hover {
                    transform: translateY(-1rem);
                    background-color: v.$color-terciario;
                    .icono {
                        color: v.$color-blanco;
                    }
                }
            }
        }
        .copy {
            color: v.$color-blanco;
            font-weight: 100;
            padding-top: 3rem;
            border-top: 0.1rem solid v.$color-blanco;
        }
    }
}

@include m.telefono {
    .contenedor-footer {
        .footer {
            .contenedor-rrss {
                width: 24rem;
            }
        }
    }
}
