@use "base/variables" as v;
@use "base/mixins" as m;

.header {
    width: 100%;
    //background: linear-gradient(135deg, rgba(96, 54, 1, 1) 0%, rgba(54, 21, 0, 1) 50%, rgba(28, 10, 0, 1) 100%);
    background-color: v.$color-secundario;
    text-align: center;
    //border-bottom-left-radius: 5rem;
    //border-bottom-right-radius: 5rem;
    position: relative;
    box-shadow: 4px 4px 16px 5px v.$color-primario;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    z-index: 40;
    padding: 2rem 1rem;
    .contenedor-logo {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row-reverse;
        cursor: pointer;
        .titulo-pagina {
            padding: 3rem;
            font-weight: v.$regular;
            color: v.$color-blanco;
        }
        .contenedor__imagen-logo {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 10rem;
            height: 10rem;
            padding: 1rem;
        }
    }
}
