@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor {
    display: flex;
    justify-content: center;
    align-items: center;
    .section__contactanos {
        margin: 5rem 2rem;
        width: 28rem;
        .contenedor__bloque-info {
            position: relative;
            input {
                position: relative;
                right: 0.4rem;
            }
            input,
            textarea {
                padding-right: 3.2rem;
            }
            .boton {
                padding: 2rem;
                right: 0;
            }

            .indicador {
                position: absolute;
                width: 1rem;
                height: 1rem;
                border-radius: 50%;
                background-color: darken(v.$color-blanco, 30%);
                top: 6.1rem;
                right: 2rem;
            }
        }
        .valido {
            .indicador {
                background-color: #0f0;
                box-shadow: 0 0 0.5rem #0f0, 0 0 1rem #0f0, 0 0 2rem #0f0, 0 0 4rem #0f0;
            }
        }
        .no--valido {
            .indicador {
                background-color: #f00;
                box-shadow: 0 0 0.5rem #f00, 0 0 1rem #f00, 0 0 2rem #f00, 0 0 4rem #f00;
            }
        }
    }
}

.contenedor--ventana-emergente {
    display: none;
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(5.5px);
    -webkit-backdrop-filter: blur(5.5px);
    border: 1px solid rgba(0, 0, 0, 0.18);

    .ventana-emergente {
        position: fixed;
        width: 28rem;
        background-color: rgba(255, 255, 255, 0.5);
        box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
        backdrop-filter: blur(5.5px);
        -webkit-backdrop-filter: blur(5.5px);
        border-radius: 10px;
        border: 1px solid rgba(255, 255, 255, 0.58);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        z-index: 30;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 1rem;

        &::before {
            content: "";
            position: relative;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: black;
            z-index: 50;
        }
        &__titulo {
            padding: 2rem;
            font-family: v.$fuente;
            font-weight: 300;
            color: red;
        }
        &__mensaje {
            padding: 2rem;
            font-weight: 400;
        }
        &__boton {
            padding: 2rem;
            button {
                padding: 1rem 4rem;
                border-radius: 0.5rem;
                border: none;
                background-color: v.$color-secundario;
                color: v.$color-cuaternario;
                box-shadow: 0 0.4rem 0.8rem 0 darken(v.$color-primario, 2%);
                cursor: pointer;
            }
        }
    }
}

@include m.telefono {
    .contenedor {
        .section__contactanos {
            width: 44rem;
        }
    }
}
@include m.tablet {
    .contenedor {
        width: 100%;
        .section__contactanos {
            width: 80rem;
            .form {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                align-items: flex-start;
                flex-direction: row;
                position: relative;
                .contenedor__bloque-info {
                    width: 33rem;

                    display: flex;
                    flex-direction: row;
                    justify-content: space-around;
                    align-items: baseline;

                    label {
                        margin-right: 1rem;
                        width: 8rem;
                    }
                    input,
                    textarea {
                        width: 24rem;
                    }
                    .indicador {
                        top: 1.9rem;
                        right: 2rem;
                    }
                }
                .contenedor__bloque-info--boton {
                    width: 100%;
                }
            }
        }
    }
}
