@use "base/variables" as v;
@use "base/mixins" as m;
/*
            nav.style.position = 'fixed';
            nav.style.zIndex = 30;
            nav.style.top = 0; //box-shadow: inset 4px 4px 16px 5px v.$color-primario;
            nav.style.boxShadow = 'inset -4px -4px 16px 5px red'; 
            
            nav.style.position = 'relative';
            nav.style.zIndex = 8;
            
            */
.menu-sticky {
    position: fixed;
    z-index: 30;
    padding-top: 1rem !important;
    top: 2rem;
}
.nav {
    background-color: v.$color-terciario;
    padding-top: 3rem;
    padding-right: 2rem;
    padding-left: 2rem;
    padding-bottom: 1rem;
    margin-top: -2rem;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    box-shadow: 4px 4px 16px 5px v.$color-primario;
    //position: fixed;
    .ocultar {
        display: none;
    }
    .menu {
        width: 100%;
        text-align: center;
        padding-left: 0;
        .contenedor-enlace {
            list-style: none;
            padding: 1rem 2rem;
            border-top: 1px solid v.$color-cuaternario;
            &:last-of-type {
                border-bottom: 1px solid v.$color-cuaternario;
            }
            .enlace {
                color: v.$color-blanco;
                font-size: 2.2rem;
            }
            &:hover {
                background-color: v.$color-cuaternario;
                border-radius: 2rem;
                transition: background-color ease 1s, border-radius ease 1s;
                cursor: pointer;
                .enlace {
                    color: v.$color-primario;
                    transition: color ease 0.3s;
                }
            }
        }
    }
}
.contenedor-icono {
    cursor: pointer;
    width: 5rem;
    height: 5rem;
    //background: linear-gradient(135deg, rgba(96, 54, 1, 1) 0%, rgba(54, 21, 0, 1) 50%, rgba(28, 10, 0, 1) 100%);
    background-color: v.$color-primario;
    box-shadow: 4px 4px 16px 5px v.$color-primario;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color ease 0.3s, color ease 0.3s;
    .icono {
        width: 4rem;
        height: 4rem;
        color: v.$color-cuaternario;
    }
    &:hover {
        background-color: v.$color-secundario;
        border-radius: 50%;
        .icono {
            color: v.$color-blanco;
        }
    }
}

@include m.tablet {
    .nav {
        .contenedor-icono {
            display: none !important;
        }
        .menu {
            display: flex;
            justify-content: space-evenly;
            width: 100%;
            .contenedor-enlace {
                padding: none;
                border-top: none;
                &:last-of-type {
                    border-bottom: none;
                }
            }
        }
    }
}
